*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background: burlywood;
    background-color: darkblue;
}
.booklist {
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;  
    gap: 2rem;
}
@media screen and (min-width: 768px){
    .booklist {
        grid-template-columns: repeat(3, 1fr);
    }
}